<template>
	<div>
		<el-container>
			<el-aside width="none">
				<el-menu
					router
					unique-opened
					:default-active="activePath"
					active-text-color="#5CC47B"
					background-color="#304156"
					text-color="#fff"
					:collapse="isCollapse"
				>
					<el-submenu
						:index="item.id + ''"
						v-for="item in menudata"
						:key="item.name"
					>
						<template slot="title">
							<i :class="item.icon"></i>
							<span>{{ item.name }}</span>
						</template>
						<el-menu-item
							:index="'/' + items.path"
							v-for="(items, index) in item.children"
							:key="items.name"
							@click="saveStale(items.path)"
						>
							{{ items.name }}
						</el-menu-item>
					</el-submenu>
				</el-menu>
			</el-aside>
			<el-container>
				<el-header>
					<el-row :gutter="20">
						<el-col :span="1">
							<div @click="collapse">
								<i v-if="!isCollapse" class="el-icon-s-unfold"></i>
								<i v-else="isCollapse" class="el-icon-s-fold"></i>
							</div>
						</el-col>
						<el-col :span="6">
							<bread-crumb />
						</el-col>
						<el-col :span="1" :offset="12">
							<i class="el-icon-rank" @click="screen"></i>
						</el-col>
						<el-col :span="4">
							<img
								v-if="!isuserimg"
								class="userimg"
								src="@/assets/img/default.jpeg"
							/>
							<el-dropdown :hide-on-click="false">
								<span class="el-dropdown-link">
									<span>{{ getusername }}</span>
								</span>
								<el-dropdown-menu slot="dropdown">
									<span @click="quit" class="quit">退出</span>
								</el-dropdown-menu>
							</el-dropdown>
						</el-col>
					</el-row>
				</el-header>
				<el-main>
					<router-view />
				</el-main>
			</el-container>
		</el-container>
	</div>
</template>

<script>
import BreadCrumb from '@/components/common/breadcrumb/BreadCrumb.vue'
import screenfull from 'screenfull'
import { mapGetters } from 'vuex'
export default {
	name: 'Home',
	components: {
		BreadCrumb,
	},
	mixins: [],
	props: {},
	data() {
		return {
			menudata: [
				{
					id: 1,
					uid: 0,
					name: '基础',
					icon: 'el-icon-s-home',
					children: [
						{
							id: 2,
							uid: 1,
							name: '公告板',
							path: 'Notice',
						},
						{
							id: 3,
							uid: 1,
							name: '文档',
							path: 'Doc',
						},
						{
							id: 4,
							uid: 1,
							name: '反馈页面',
							path: 'pages',
						},
					],
				},
				{
					id: 30,
					uid: 0,
					name: '表格',
          icon:'el-icon-s-grid',
					children: [
						{
							id: 31,
							uid: 30,
							name: '列表',
							path: 'Table',
						},
						{
							id: 32,
							uid: 30,
							name: '查询表格',
							path: 'seletab',
						},
						{
							id: 33,
							uid: 30,
							name: '标准表格',
							path: 'tab',
						},
					],
				},
			],
			//选中
			activePath: '',
			//是否关闭菜单
			isCollapse: false,
			isuserimg: false,
		}
	},
	computed: {
		/*username() {
      if(this.$route.params.username!='') {
         return this.$route.params.username
      }}*/
		...mapGetters(['getusername']),
	},
	watch: {},
	created() {
    console.log('重新创建!');
		console.log(this.$store, 'store')
		this.activePath = window.sessionStorage.getItem('activePath')
		//默认选中状态
		if (this.activePath == null) {
			console.log(this.activePath)
			this.activePath = '/Notice'
		}
	},
	mounted() {},
	destroyed() {},
	methods: {
		//点击选中保持状态
		saveStale(path) {
			window.sessionStorage.setItem('activePath', '/' + path)
		},
		//点击关闭菜单栏
		collapse() {
			this.isCollapse = !this.isCollapse
		},
		//全屏显示
		screen() {
			screenfull.toggle()
		},
		//退出
		quit() {
			console.log('quit')
			this.$router.replace('/Login')
		},
	},
}
</script>
<style lang='less' scoped>
.el-menu {
	height: 100vh;
}
.el-header .el-row:nth-child(1) {
	line-height: 60px;
}
.el-header .el-row:nth-child(1) i {
	font-size: 23px;
	color: #404040;
}
.el-main {
	min-width: 1000px;
	background-color: #f0f2f5;
}
.userimg {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	vertical-align: middle;
	margin-right: 10px;
}
.el-dropdown-link {
	cursor: pointer;
}
.quit {
	display: block;
	padding: 0 20px 0 20px;
	cursor: pointer;
	font-size: 18px;
	color: #606266;
}
.quit:hover {
	background-color: rgba(0, 0, 0, 0.1);
	color: #409eff;
}
</style>
