<template>
  <div>
    <el-breadcrumb
    separator="/"
     >
      <el-breadcrumb-item
      >
      <a href="#">{{ breadcrumblist[0].parentName}}</a> 
      </el-breadcrumb-item>
      <el-breadcrumb-item>
        {{ breadcrumblist[0].title}}
      </el-breadcrumb-item>
		</el-breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'BreadCrumb',
  components: {},
  mixins:[],
  props: {},
  data () {
    return {
      breadcrumblist:[]
    }
  },
  computed: {},
  watch: {
    //异步监听$route
      $route:{
       handler(to) {
           //to相当于$route对象
           console.log(to);
          if(to.meta) {
            this.breadcrumblist.push(to.meta)
          }

      },
      immediate:true

    }
  },
  created () {},
  mounted () {
  },
  destroyed () {},
  methods: {},
}

</script>
<style lang='less' scoped>
   .el-breadcrumb {
    line-height: 60px;
  }
</style>
